import { Component, OnInit } from '@angular/core';
import * as M from 'materialize-css';
import { ModalService } from './notificationModalService';
import { SelectorFlags } from '@angular/compiler/src/core';
@Component({
  selector: 'app-notification-modal',
  templateUrl: './notification-modal.component.html',
  styleUrls: ['./notification-modal.component.scss']
})
export class NotificationModalComponent implements OnInit {

  public messageHeader: string;
  public messageBody: string;
  public modalInstance: any;
  public modalElements: any;
  
  constructor(private modalService: ModalService) { }

  ngOnInit() {
    this.initModal();
  }

  /* get Message Header */
   public getMessageHeader(): string
   {
     return this.messageHeader;
   }

   /* End of getting MessageBody */

   /* get Message Header */
   public getMessageBody(): string
   {
     return this.messageBody;
   }

   /* End of getting MessageBody*/

   /* call this method for opening the modal */
   public openModal(): void
   {
     this.modalInstance[0].open();
   }
   /* End of call this method for opening the modal */


   /* call this method for opening the modal */
   public closeModal(): void
   {
     this.modalInstance[0].close();
   }
   /* End of call this method for opening the modal *



  /* initialise the modal */
 private initModal(): void
  {
    const self = this;
    document.addEventListener('DOMContentLoaded', () => {
      self.modalElements = document.querySelectorAll('.modal');
      self.modalInstance = M.Modal.init(self.modalElements, {dismissible: false});
      self.getModalService();
    });

  }
    /* initialise the modal */


   public userResponse(type: string)
    {
      this.modalService.notifySubscriber.next(type);
    }

   public getModalService(): void
   {
    this.modalService.getModalStatus().subscribe((modalObj: any)=>{
        this.messageHeader = modalObj.header;
        this.messageBody = modalObj.message;
        if(modalObj.status)
        {
          this.openModal();
        } else {
          this.closeModal();
        }
    });
   }


}
