import { Injectable } from '@angular/core';
import * as M from 'materialize-css';

@Injectable({
  providedIn: 'root'
})
export class NotifyService {

  constructor() { }

   // for showing the toast
  public showToast(message: string, status: string)
   {
    switch (status) {
      case 'error':
      M.toast({html: message, classes: 'color: red'});
      break;
        case 'success':
        M.toast({html: message, classes: 'color: green'});
        break;
      default:
      M.toast({html: message, classes: 'color: black'});
      break;
    }
   }
}
