import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import {NavComponent} from './nav/nav.component';
import { LoaderComponent } from './loader/loader.component';
import { NavService } from './nav/nav.service';
import { LoaderService } from './loader/loader.service';
import { FooterComponent } from './footer/footer.component';
import { NotificationModalComponent } from './notification-modal/notification-modal.component';
import { ModalService} from './notification-modal/notificationModalService';
import { RouterModule } from '@angular/router';
import { AppServiceModule } from './services/appService.module';



/*
  * This is shared modules which consist of:-
    1) Loader
    2) Toast
    3) NavBar
    4) Modals

 */

@NgModule({
    declarations: [NavComponent, LoaderComponent, FooterComponent, NotificationModalComponent],
    imports: [ CommonModule, RouterModule, AppServiceModule.forRoot()],
    exports: [NavComponent, LoaderComponent, FooterComponent, NotificationModalComponent],
    providers: []
})
export class SharedModule {

  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SharedModule,
      providers: [
        LoaderService,NavService,ModalService
      ]
    };
  }
}