import { Injectable } from '@angular/core';
import { Subject, BehaviorSubject, Observable } from 'rxjs';

@Injectable()
export class ModalService {

    private modal: Subject<any> = new BehaviorSubject(false);
    public notifySubscriber: Subject<any> = new BehaviorSubject('');

    public openModal(header: string, message: any): Observable<any>
    {
         this.modal.next({header, message, status: true});
         return this.notifySubscriber.asObservable();
    }

    public closeModal(): void
    {
        this.modal.next({header: '', message: '', status: false});
    }

    public getModalStatus(): Observable<any>
    {
        return this.modal.asObservable();
    }

}