import { Injectable } from '@angular/core';
import CoreJS from '../../../../../PlatwareClient/pwCore';

@Injectable()
export class EncryptionService {
  constructor() {}

  public encryptValue(value: string) {
    return CoreJS.AESEnc(value, 'decimalsecretkey');
  }

  public decryptValue(value: string) {
    return CoreJS.AESDec(value, 'decimalsecretkey');
  }
}
