  /* This method copmares the two date and return boolean */
 public compareTwoDates( firstDateString: string, secondDateString: string): boolean {
  return   ( Date.parse ( firstDateString ) > Date.parse ( secondDateString ));
 }


 /* This method copmares the two date and return boolean */
 public compareDates( validateTo: string): boolean {
  return   Date.parse (validateTo) >= new Date().getTime();
 }
/* End of This method copmares the two date and return boolean */

public getUpdatedArray(array: Array<any>, type: string, id: string): Array<any> {
  switch (type) {
    // tslint:disable-next-line:object-literal-key-quotes
    case 'ADD': return _.concat(array, [{ 'id': id, 'value': true }]);
    case 'REMOVE': return _.dropRightWhile(array,  (o) => o.id === id);
  }
}

public getQueryStringValue(href, key): string {
  const url = decodeURI(decodeURIComponent(href));
  return this.getQueryString(key, url);
}

public getQueryString(field, url): string {
  const href = url ? url : window.location.href;
  const reg = new RegExp('[?&]' + field + '=([^&#]*)', 'i');
  const stringValue = reg.exec(href);
  return stringValue ? stringValue[1] : null;
}

public getQueryObj(url) {
    const params = {};
    // tslint:disable-next-line:only-arrow-functions
    (url + '?').split('?')[1].split('&').forEach((pair: any) => {
      pair = (pair + '=').split('=').map(decodeURIComponent);
      if (pair[0].length) {
        params[pair[0]] = pair[1];
      }
    });
    return params;
}

