/* Return the date in ISO Format */
public getDateInISOFormat(): string {
 return new Date().toISOString();
}
/* End of Return the date in ISO Format */

/* Start of getting localStorage Value */
public getLocalStorage(key: string): string {
   const value = localStorage.getItem(key);
   if (value) {
    return this.encryptionService.decryptValue(value);
   } else {
     return null;
   }

}

/* End of getting localStorage Value */


public setLocalStorage(key: string, value: string): void {
  const encryptedValue = this.encryptionService.encryptValue(value);
  localStorage.setItem(key, encryptedValue);
}

public removeKeyFromLocalStorage(key: string) {
  localStorage.removeItem(key);
}

public clearLocalStorage() {
  localStorage.clear();
}


/* method will return any value from based on key from config*/
public getValueFromConfig(key: string): string {
  const value = this.getLocalStorage('config');
  if (value) {
   const config = JSON.parse((value));
   return  config[key];
  }
  return null;

}

/* End of getting localStorage Value */



