/* save user details */

public saveUserDetail(response){
  
  response[AppConstant.ORG_DESC] = response.orgDescription
  response[AppConstant.ORG_DOMAIN] = response.orgDomain;
  response[AppConstant.ORG_ID] = response.orgId;
  response[AppConstant.DEFAULT_ORG_ID] = response.orgId;
  response[AppConstant.ORG_LOGO] = response.orgLogo;
  response[AppConstant.ORG_NAME] = response.orgName;
  response[AppConstant.DEFAULT_ORG_NAME] = response.orgName;
  response[AppConstant.EMAIL] = response.userDetails.email;
  response[AppConstant.MOBILE] = response.userDetails.mobileNumber;
  response[AppConstant.ORGANIGATION_NAME] = response.userDetails.organisationName;
  response[AppConstant.USER_NAME] = response.userDetails.userName;
  
  console.log(response);
  this.setLocalStorage(AppConstant.USER_DETAIL,JSON.stringify(response));
}

public updateUserDetail(key: string, value: string) {
  let userDetail = this.getLocalStorage(AppConstant.USER_DETAIL);
  if (userDetail) {
    userDetail = JSON.parse((userDetail));
    userDetail[key] = value;
    this.setLocalStorage(AppConstant.USER_DETAIL, JSON.stringify(userDetail));
  }
}

/* method will return any value from based on key from config*/
public getValueFromUserDetail(key: string): string {
  const value = this.getLocalStorage(AppConstant.USER_DETAIL);
  if (value) {
   const config = JSON.parse((value));
   return  config[key];
  }
  return null;
}

