import { Component, OnInit, Input, AfterViewInit, OnDestroy } from '@angular/core';
import * as M from 'materialize-css';
import { NavService } from './nav.service';
import { Subscription, from, Observable, BehaviorSubject } from 'rxjs';
import { Location } from '@angular/common';
import { NavConfig } from './nav.interface';
import { Router } from '@angular/router';

@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css']
})
export class NavComponent implements OnInit, AfterViewInit, OnDestroy {

  private headerNameSubsription: Subscription;
  private clientDetailSubscription: Subscription;
  public clientDetail: any;
  public headerName: String = 'Home';
  public navConfig: NavConfig;
  public instance: any;
  public backGroundColor: string;
  public logo = new  BehaviorSubject<boolean>(false);
  public icon = new BehaviorSubject<boolean>(false);
  public icons = {}
  constructor(private navService: NavService, private location: Location,
              private router: Router) {}

  ngOnInit() {
    this.headerNameSubsription = this.navService.headerName.subscribe((value) => {
      this.headerName = value;
    });
    this.clientDetailSubscription = this.navService.getClientDetail().subscribe((clientDetail) => {
      this.clientDetail = clientDetail;
    });
    this.getBackGroundColor();
    this.setLogo();
  }

  public isIconPresent(): Observable<boolean> {
    return this.icon.asObservable();
  }
  public isLogoPresent() {
    return this.logo.asObservable();
  }
  private setLogo() {
    // this.campaignService.getLogo().subscribe((value) => {
    //   if(value){
    //   const logo = value['logo'];
    //   const icon = value['icon'];
    //   this.icons['logo'] = logo;
    //   this.icons['icon'] = icon;
    //   if (logo) {
    //     return this.logo.next(true);
    //   }
    //   if (icon) {
    //     return this.icon.next(true);
    //   }

    // }

    // });
  }
  ngAfterViewInit(): void {
    this.sideNav();
  }

  public sideNav(): void {
    const elems = document.querySelectorAll('.sidenav');
    // tslint:disable-next-line:curly
    if (elems) this.instance = M.Sidenav.init(elems, {});
  }

  public goBack(): void {
    this.location.back();
    this.navService.setHideBackButton(true);
  }


  ngOnDestroy(): void {
    if (this.headerNameSubsription !== undefined) {
      this.headerNameSubsription.unsubscribe();
      this.clientDetailSubscription.unsubscribe();
    }
  }

  /* This method set the color of NavBar */
  public getBackGroundColor(): void {
     this.navService.getBackGroundColor().subscribe((color: string) => {
      this.backGroundColor = color;
     });
  }

  /* End-------------------------*/

  public getHideNavBarStatus(): Observable<boolean> {
       return this.navService.getHideNavBar();
  }

  public getHideBackButtonStatus(): Observable<boolean> {
   return this.navService.getHideBackButton();
  }


  
}
