import { Injectable } from '@angular/core';
import { Subject, BehaviorSubject, Observable } from 'rxjs';

@Injectable()
export class NavService {
    public headerName: Subject<string> = new BehaviorSubject('Home');
    public clientDetail: Subject<any> = new BehaviorSubject('');
    public backGroundColor: Subject<string> = new BehaviorSubject('#2457a7');
    private hideBackButton: Subject<boolean> = new BehaviorSubject(true);
    private hideNavBar: Subject<boolean> = new BehaviorSubject(false);
    /* set client Detail */
    public setClientDetail(clientDetail: any)
    {
       this.clientDetail.next(clientDetail);
    }

     /* End of set client Detail */

    /* get client Detail */ 
    public getClientDetail()
    {
        return this.clientDetail.asObservable();
    }
    /* End of get client Detail */

    public setNavHeaderName(name: string)
    {
       this.headerName.next(name);
    }

    public getNavHeaderName()
    {
        return this.headerName.asObservable();
    }

    public setBackgroundColor(color: string)
    {
      this.backGroundColor.next(color);
    }

    public getBackGroundColor()
    {
        return this.backGroundColor.asObservable();
    }

   public setHideBackButton(flag: boolean): void
   {
       this.hideBackButton.next(flag);
   }

   public getHideBackButton(): Observable<boolean>
   {
       return this.hideBackButton.asObservable();
   }

   public setHideNavBar(flag: boolean): void
   {
      this.hideNavBar.next(flag);
   }

   public getHideNavBar(): Observable<boolean>
   {
     return this.hideNavBar.asObservable();
   }
}